a {
  @if $_link-reverse {
    color: $_color-primary;
    text-decoration: underline;

    &:hover {
      color: $_color-base;
      text-decoration: none;
    }
  } @else {
    color: $_color-primary;
    text-decoration: none;

    &:hover {
      color: $_color-base;
      text-decoration: underline;
    }
  }
}
