///
// Mixin font size
//
// Generating font setting mixin
//
// ```
// @include container(1300px);
//
// // output
// max-width: 1300px;
// width: 100%;
// margin-left: auto;
// margin-left: auto;
// ```

@mixin container($max-width: 1200px) {
  max-width: $max-width;
  width: 100%;
  margin-left: auto;
  margin-left: auto;
}

